* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  text-align: center;
  width: 700px;
  border: 3px solid #e4e4e4;
  border-radius: 10px;
  justify-content: center;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  box-shadow: inset 0 -3em 3em rgba(0, 0, 0, .1), .3em .3em 1em rgba(0, 0, 0, .3);
}

.filed, .field .value {
  font-size: 50px;
}

